import React, { useState, useEffect } from "react";
import socketIOClient from "socket.io-client";
import { useNiconico } from "react-niconico";
import Confetti from "react-confetti";
import useSound from "use-sound";
import boopSfx from "./sound1.mp3";
import "./App.css";
var socket;
const animation = {
    likes50:
        "https://ynassets.younow.com/gifts/live/10_LIKES/ios_icon_gift_10_LIKES_bar@3x.png?2",
    corazon:
        "https://ynassets.younow.com/gifts/live/RED_HEART/ios_icon_gift_RED_HEART_bar@3x.png?2",
};
socket = socketIOClient();

function App() {
    const [party, setParty] = useState(true);
    const [play] = useSound(boopSfx);

    const [ref, emitText] = useNiconico({
        displayMillis: 10000,
        lineWidth: 3,
        //lineWidth: 10,
    });
    useEffect(() => {
        socket.on("connect", () => {
            console.log("Successfully connected!");
            console.log(socket.id);
        });
        socket.on("get_message", (msg) => {
            console.log("get_message", msg);
            if (msg.text) emitText(String(msg.text));
        });
        socket.on("get_gifts", (msg) => {
            console.log("get_gifts", msg);
            play();
            if (msg.url === animation.likes) {
                console.log("50likes");
                play();
            }
            setParty(true);
        });
        socket.on("get_audience", (msg) => {
            console.log("get_audience", msg);
        });

        emitText("Configurado");
        play();
    }, [emitText]);

    return (
        <div>
            <div className="App App-header" ref={ref}></div>
            <Confetti
                style={{ width: "100vw", height: "100vh" }}
                numberOfPieces={party ? 80 : 0}
                recycle={false}
                onConfettiComplete={(confetti) => {
                    setParty(false);
                    confetti.reset();
                }}
                //drawShape={drawStar}
            />
        </div>
    );
}

export default App;
